@mixin style-proyect {

    .proyect__text {
        color: $whiteText2;
        font-size: 0.85rem;
        min-height: 34px;
        margin: auto;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@mixin button-icon {
    display: flex;
    align-content: center;
    margin: 0 10px;
    transition: all ease-in-out 0.3s;
    cursor: pointer;
    filter: drop-shadow(2px 2px 4px #000);

    &:hover {
        filter: brightness(0) saturate(100%) invert(57%) sepia(100%) saturate(980%) hue-rotate(335deg) brightness(100%) contrast(103%) drop-shadow(2px 2px 4px #000);
        -webkit-filter: brightness(0) saturate(100%) invert(57%) sepia(100%) saturate(980%) hue-rotate(335deg) brightness(100%) contrast(103%) drop-shadow(2px 2px 4px #000);
    }
}

@mixin button-primary {
    background-color: $secondary;
    color: black;
    font-size: 1rem;
    border: none;
    border-radius: 4px;
    width: 10rem;
    height: 2.5rem;
    box-shadow: 2px 2px 5px #000;
    font-weight: 700;
    cursor: pointer;
    transition: all ease-in-out 0.3s;

    &:hover {
        background-color: hsl(25, 100%, 52%);
    }

    &:active {
        background-color: hsl(25, 100%, 58%);
        box-shadow: none;
    }
}

@mixin form-errors {
    color: $secondary;
    position: relative;
    width: 0;
    height: 0;
    margin: 0;
}

@mixin form-input {
    color: #fefefe;
    border: 1px solid black;
    outline: none;
    border-radius: 2px;
    padding-left: 1rem;
    box-shadow: 2px 4px 6px inset $shadow;
    height: 32px;
}